.comp-1-3-loading {
  align-items: center;
  filter: blur(10px);
  height: 105px;
  justify-content: center;
  min-width: 103px;
  opacity: 0.2;
}

.comp-1-3-loading.animate-enter7 {
  animation: animate-enter7-frames 2.00s ease-in-out 0.00s 1 normal forwards;
  filter: blur(10px);
  opacity: 0.2;
}

.comp-1-12x-1 {
  height: 152px;
  margin-left: 1px;
  margin-top: 1px;
  object-fit: cover;
  width: 152px;
}

@keyframes animate-enter7-frames {
  from{opacity: 0.2;
filter: blur(10px);
}
to{opacity: 1;
filter: none;
}
}

