.welcome {
  align-items: center;
  background: linear-gradient(180deg, rgb(247.56, 247.56, 247.56) 0%, rgb(255, 255, 255) 100%);
  background-color: rgba(255,255,255,1.0);
  border: 1px none;
  display: flex;
  flex-direction: column;
  gap: 20px;
  height: 100vh;
  justify-content: center;
  min-height: 711px;
  min-width: 337px;
  padding: 49px 17px;
  position: relative;
  width: 100%;
}

.hello-my-name-is-ga-welcome {
  height: 100px;
  letter-spacing: 0;
  line-height: normal;
  text-align: center;
  transform: scale(1.1);
  width: 125px;
}

.hello-my-name-is-ga-welcome.animate-enter4 {
  animation: animate-enter4-frames 3s ease-out 0s 1 normal forwards;
  transform: scale(2);
}

.copyright-welcome {
  height: 13px;
  letter-spacing: 0;
  line-height: normal;
  text-align: center;
  transform: scale(1.1);
  width: 125px;
}

.copyright-welcome.animate-enter6 {
  animation: animate-enter6-frames 0.50s ease-in-out 0.00s 1 normal forwards;
  transform: scale(1.1);
}

@keyframes animate-enter4-frames {
  from{opacity: 1;
transform: scale(1.1);
}
to{opacity: 1;
transform: scale(1);
}
}

@keyframes animate-enter6-frames {
  from{opacity: 1;
transform: scale(1.1);
}
to{opacity: 1;
transform: scale(1);
}
}

