.main-button-aboutpage {
  -webkit-backdrop-filter: blur(4px) brightness(100%);
  align-items: center;
  backdrop-filter: blur(4px) brightness(100%);
  background-color: var(--white-2);
  border: 1px solid;
  border-color: var(--chicago);
  border-radius: 22px;
  bottom: 36px;
  box-shadow: 0px 4px 4px #00000040;
  filter: blur(15px);
  gap: 10px;
  justify-content: center;
  left: calc(50.00% - 50px);
  opacity: 0;
  padding: 2px 28px;
  position: fixed;
  width: fit-content;
  z-index: 2;
}

.main-button-aboutpage.animate-enter3 {
  animation: animate-enter3-frames 1.50s ease-in-out 0.00s 1 normal forwards;
  filter: blur(15px);
  opacity: 0;
}

.back {
  letter-spacing: 0;
  line-height: normal;
  margin-top: -1.00px;
  text-align: center;
  width: fit-content;
}

.main-button-aboutpage.main-button-1-welcome {
  -webkit-backdrop-filter: unset;
  backdrop-filter: unset;
  background-color: unset;
  bottom: unset;
  box-shadow: 0px 4px 4px #0000000d;
  filter: unset;
  left: unset;
  opacity: unset;
  position: unset;
  transform: scale(1.1);
  z-index: unset;
}

.main-button-aboutpage.main-button-1-welcome.animate-enter5 {
  animation: animate-enter5-frames 1.50s ease-in-out 0.30s 1 normal forwards;
  transform: scale(1.1);
}

.main-button-aboutpage.main-button-2-archivepage {
  left: calc(50.00% - 48px);
}

.main-button-aboutpage.main-button-2-archivepage.animate-enter14 {
  animation: animate-enter14-frames 1.50s ease-in-out 0.00s 1 normal forwards;
  filter: blur(15px);
  opacity: 0;
}

@keyframes animate-enter14-frames {
  from{opacity: 0;
filter: blur(15px);
}
to{opacity: 1;
filter: none;
}
}

@keyframes animate-enter3-frames {
  from{opacity: 0;
filter: blur(15px);
}
to{opacity: 1;
filter: none;
}
}

@keyframes animate-enter5-frames {
  from{opacity: 1;
transform: scale(1.1);
}
to{opacity: 1;
transform: scale(1);
}
}

