.frame-1 {
  align-items: flex-end;
  background-color: var(--white);
  border: 1px none;
  display: flex;
  flex-direction: column;
  gap: 44px;
  min-height: 711px;
  padding: 214px 99px;
  position: relative;
  width: 314px;
}

.gz-102-grayzapp-frame-1 {
  color: transparent;
  cursor: pointer;
  font-family: var(--font-family-suisse_intl-medium);
  font-size: var(--font-size-l);
  font-weight: 400;
  letter-spacing: 0;
  line-height: normal;
  opacity: 0;
  text-align: center;
  transform: scale(1.2);
  transition: all 0.4s ease;
  width: 115px;
}

.span0 {
  color: var(--chicago);
  font-weight: 500;
}

.span1 {
  color: #dcdddd;
  font-family: var(--font-family-suisse_intl-regular);
}

.gz-102-grayzapp-frame-1.animate-enter12 {
  animation: animate-enter12-frames 3s ease 0.00s 1 normal forwards;
  opacity: 0;
  transform: scale(2);
}

.gz-102-grayzapp-frame-1:hover {
  transform: scale(1.1);
}

@keyframes animate-enter12-frames {
  from{opacity: 0;
transform: scale(1.2);
}
to{opacity: 1;
transform: scale(1);
}
}

