.main-button-4-start-menu {
  align-items: center;
  background-color: transparent;
  border: 1px solid;
  border-color: var(--chicago);
  border-radius: 22px;
  box-shadow: 0px 4px 4px #0000000d;
  display: block;
  gap: 10px;
  height: auto;
  justify-content: center;
  mix-blend-mode: normal;
  opacity: 1;
  padding: 2px 28px;
  transform: translate(0, 2000px);
  width: 108px;
}

.main-button-4-start-menu.animate-enter9 {
  animation: animate-enter9-frames 2s ease 0.30s 1 normal forwards;
  display: block;
  opacity: 1;
  transform: translate(0, 2000px);
}

.about-2 {
  letter-spacing: 0;
  line-height: normal;
  margin-left: -3.50px;
  margin-right: -3.50px;
  margin-top: -1.00px;
  text-align: center;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

@keyframes animate-enter9-frames {
  from{opacity: 1;
transform: translate(0, 2000px);
}
to{opacity: 1;
transform: translate(0,0);
}
}

