.archivepage {
  background-color: var(--white);
  border: 1px none;
  display: flex;
  height: 100vh;
  justify-content: center;
  min-height: 8610px;
  min-width: 344px;
  position: relative;
  width: 100%;

}

.archives-archivepage {
  align-items: center;
  filter: blur(10px);
  flex-direction: column;
  gap: 17px;
  height: 8610px;
  opacity: 0;
  padding: 79px 0px 51px;
  width: 300px;
  z-index: 1;
}

.archives-archivepage.animate-enter13 {
  animation: animate-enter13-frames 1.50s ease-in-out 0.10s 1 normal forwards;
  filter: blur(10px);
  opacity: 0;
}

