.loading {
  align-items: center;
  background-color: var(--white);
  border: 1px none;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  gap: 33px;
  height: 100vh;
  justify-content: center;
  min-height: 711px;
  min-width: 337px;
  padding: 49px 17px;
  position: relative;
  width: 100%;
}

.click {
  height: 13px;
  letter-spacing: 0;
  line-height: normal;
  text-align: center;
  width: 125px;
}

