.aboutpage {
  border: 1px none;
  display: flex;
  height: 100vh;
  min-height: 711px;
  min-width: 340px;
  position: relative;
  width: 100%;
}

.about {
  align-items: center;
  background-color: var(--white);
  display: flex;
  flex: 1;
  flex-direction: column;
  gap: 26px;
  height: 711px;
  justify-content: center;
  padding: 173px 106px;
  width: 340px;
  z-index: 1;
}

.gabriel-dellaiuto-about {
  letter-spacing: 0;
  line-height: 13.2px;
  margin-left: -42.00px;
  margin-right: -42.00px;
  margin-top: -13.50px;
  text-align: justify;
  transform: translate(5000px, 0);
  width: 212px;
}

.gabriel-dellaiuto-about.animate-enter {
  animation: animate-enter-frames 1.50s ease-in-out 0.10s 1 normal forwards;
  transform: translate(5000px, 0);
}

.download-rsum-about {
  color: var(--chicago);
  cursor: pointer;
  font-family: var(--font-family-suisse_intl-medium);
  font-size: var(--font-size-m);
  font-weight: 500;
  letter-spacing: 0;
  line-height: 13.2px;
  margin-left: -42.00px;
  margin-right: -42.00px;
  text-align: center;
  text-decoration: underline;
  transform: translate(2000px, 0);
  transition: all 0.3s ease-out;
  white-space: nowrap;
  width: 212px;
}

.download-rsum-about.animate-enter1 {
  animation: animate-enter1-frames 1.00s ease-in-out 0.30s 1 normal forwards;
  transform: translate(2000px, 0);
}

.download-rsum-about:hover {
  transform: scale(1.1);
}

.gz-or-grayz-gz-or-about {
  letter-spacing: 0;
  line-height: 13.2px;
  margin-bottom: -11.50px;
  margin-left: -42.00px;
  margin-right: -42.00px;
  text-align: justify;
  transform: translate(2000px, 0);
  width: 212px;
}

.span3 {
  text-decoration: underline;
}

.gz-or-grayz-gz-or-about.animate-enter2 {
  animation: animate-enter2-frames 1.00s ease-in-out 0.20s 1 normal forwards;
  transform: translate(2000px, 0);
}

@keyframes animate-enter2-frames {
  from{opacity: 1;
transform: translate(2000px, 0);
}
to{opacity: 1;
transform: translate(0,0);
}
}

@keyframes animate-enter-frames {
  from{opacity: 1;
transform: translate(5000px, 0);
}
to{opacity: 1;
transform: translate(0,0);
}
}

@keyframes animate-enter1-frames {
  from{opacity: 1;
transform: translate(2000px, 0);
}
to{opacity: 1;
transform: translate(0,0);
}
}

