:root { 
  --black: #000000;
  --chicago: #5e5e5e;
  --white: #ffffff;
  --white-2: #ffffff66;
 
  --font-size-l: 13px;
  --font-size-m: 12px;
 
  --font-family-suisse_intl-light: "Suisse Intl-Light", Helvetica;
  --font-family-suisse_intl-medium: "Suisse Intl-Medium", Helvetica;
  --font-family-suisse_intl-regular: "Suisse Intl-Regular", Helvetica;
}
.suisseintl-regular-normal-chicago-13px {
  color: var(--chicago);
  font-family: var(--font-family-suisse_intl-regular);
  font-size: var(--font-size-l);
  font-style: normal;
  font-weight: 400;
}

.suisseintl-regular-normal-chicago-12px {
  color: var(--chicago);
  font-family: var(--font-family-suisse_intl-regular);
  font-size: var(--font-size-m);
  font-style: normal;
  font-weight: 400;
}

.suisseintl-light-chicago-12px {
  color: var(--chicago);
  font-family: var(--font-family-suisse_intl-light);
  font-size: var(--font-size-m);
  font-style: normal;
  font-weight: 300;
}

.suisseintl-light-chicago-13px {
  color: var(--chicago);
  font-family: var(--font-family-suisse_intl-light);
  font-size: var(--font-size-l);
  font-style: normal;
  font-weight: 300;
}
